import {Autocomplete, Box, InputAdornment, TextField, Typography} from "@mui/material";
import SearchIcon from '@mui/icons-material/Search';

import kamen from './static/kamen.jpg'
import {Fragment, useEffect, useState} from "react";


export default function SearchPage() {
    const [medicines, setMedicines] = useState([]);

    useEffect(() => {
        fetch('/get_medicines').then(r => r.json()).then(json => setMedicines(json));
    }, []);

    const onChange = (e, values) => {
        if (values === null) return

        window.open(values.link, '_blank', 'noreferrer');
    }

    return (
        <Fragment>
            <Box display={"flex"}>
                <Box display={"flex"} justifyContent={"flex-end"} alignItems={"center"} width={1/2}>
                    <Box mt={'6%'} display={"flex"} justifyContent={"center"} alignItems={"center"} color={"gray"}>
                        <Typography fontSize={28}>
                            PharmoKAL
                        </Typography>
                    </Box>
                </Box>
                <Box display={"flex"} justifyContent={"flex-end"} alignItems={"center"} width={1/2}>
                    <Box display={"flex"} justifyContent={"center"} alignItems={"center"} color={"gray"}>
                        Powered by <img src={kamen} alt="Kamen" width={100}/>
                    </Box>
                </Box>
            </Box>
            {/*<Box display={"flex"} justifyContent={"flex-end"} alignItems={"center"}>*/}
            {/*    <Box display={"flex"} justifyContent={"center"} alignItems={"center"} color={"gray"}>*/}
            {/*        Powered by <img src={kamen} alt="Kamen" width={100}/>*/}
            {/*    </Box>*/}
            {/*</Box>*/}
            <Box display={"flex"} justifyContent={"center"} mt={'2%'}>
                <Box width={4 / 10}>
                    <Autocomplete
                        freeSolo
                        onChange={onChange}
                        options={medicines}

                        getOptionLabel={ option => option.title }


                        renderInput={(params) => (
                            <TextField
                                {...params}
                                label="Search by name"
                                // placeholder={'Start typing the name of the required medicine...'}

                                InputProps={{
                                    ...params.InputProps,
                                    startAdornment: (
                                        <InputAdornment position="start">
                                            <SearchIcon/>
                                        </InputAdornment>
                                    ),
                                }}
                            />
                        )}
                    />
                </Box>
            </Box>
        </Fragment>
    )
}